import {graphql, withPrefix} from 'gatsby';
import Img from 'gatsby-image';
import React from 'react';

import {Button} from '../../../components/button';
import {CaseHeader} from '../../../components/case_header';
import {WillAnimate} from '../../../components/content_animate';
import {Layout} from '../../../components/layout';
import {ScrollTo} from '../../../components/scroll_to';

const locale = 'en';
const translatedPagePath = '/nl/portfolio/mobiele-wegsignalering-tti-tactic/';

export default ({data}: any) => (
    <Layout
        title="Case: Mobile road signaling TTI TACTIC – Creating software for a traffic information system – Showcase of our work"
        description="Operating a physical product in acute situations requires reliability. We develop native mobile apps for the TACTIC for work and roadside assistance."
        ogImage="tactic.png"
        translatedPagePath={translatedPagePath}
        navColor="white"
        locale={locale}>
        <CaseHeader
            urlBack="/en/portfolio/nohrd-bike-apps/"
            titleBack="Case: Apps for NOHrD Bike"
            urlForward="/en/portfolio/waterrower-connect-apps/"
            titleForward="Case: Apps for WaterRower"
            translatedPagePath={translatedPagePath}
            locale={locale}>
            <Img className="hero__image" alt="RP3" fluid={data.tactic.childImageSharp.fluid} critical />
            <div className="hero__content-wrapper">
                <div className="hero__content">
                    <h1>
                        {/* Make sure to have the correct spacing when br becomes invisible */}A modern, safe way of
                        working
                    </h1>
                    <h2>Apps for the TACTIC mobile motorway signaling</h2>
                </div>
            </div>
            <ScrollTo className="hero__scroll" target="/en/portfolio/mobile-road-signaling-tti-tactic/#from-hero" />
        </CaseHeader>

        <section className="section">
            {/* Case intro */}
            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display content__display--intro">
                        <h2>
                            TTI Partners focuses its energies on improving safety on our roads. With the TACTIC apps, we
                            make it easy for drivers of assistance vehicles that provide information to road users
                            through the operation of a new innovative{' '}
                            <span className="highlight">mobile information system</span>.
                        </h2>
                    </div>
                </div>
            </WillAnimate>

            {/* Case assignment */}
            <WillAnimate as="article" id="from-hero" className="content">
                <div className="container">
                    <div className="content__display content__display--right">
                        <h2>The assignment</h2>
                    </div>

                    <div className="content__copy">
                        <p className="p-lead">
                            The TACTIC is a mobile traffic information system made by TTI Partners. When designing the
                            TACTIC, this automotive joint venture had various key principles in mind, with{' '}
                            <span className="highlight">safety</span>, <span className="highlight">ease of use</span>{' '}
                            and <span className="highlight">flexibility</span> taking a particularly high priority.
                        </p>
                        <p>
                            When TTI Partners decided to work with us to develop the corresponding operating application
                            that would enshrine the same principles, we were very excited to be involved.
                        </p>
                        <p>
                            When a dangerous traffic situation arises, traffic assistance vehicles are used to warn road
                            users about possible dangers. Inattention sometimes leads to accidents involving road users
                            and these assistance vehicles.
                        </p>
                        <p>
                            With the new application, it would be possible to operate the TACTIC either from the
                            vehicle’s dashboard or from a <span className="highlight">safe distance</span> away{' '}
                            <span className="highlight">behind the crash barrier</span>. This would make it much safer
                            for the drivers of these assistance vehicles to do their job.{' '}
                        </p>
                        <ul className="list">
                            <li className="ion-md-arrow-dropright">UX- en UI-design</li>
                            <li className="ion-md-arrow-dropright">Wireless control of the TACTIC</li>
                            <li className="ion-md-arrow-dropright">Native iOS and Android applications</li>
                        </ul>
                    </div>
                </div>
            </WillAnimate>

            {/* Article image and role */}

            <WillAnimate as="article" className="content">
                <div className="image">
                    <div className="image__content">
                        <video
                            width="100%"
                            controls
                            autoplay="autoplay"
                            loop
                            muted
                            poster={withPrefix('/images/tactic-placeholder.jpg')}>
                            <source src={withPrefix('/video/tactic.mp4')} type="video/mp4" />
                        </video>
                    </div>
                    <div className="image__caption">
                        <span className="ion-md-arrow-dropright">
                            The TACTIC is used to provide information to road users in a safe way. The interactive
                            interface makes it very easy to set up your own scenarios and operate the TACTIC just by
                            pushing a few buttons.
                        </span>
                    </div>
                </div>
            </WillAnimate>

            {/* Case challenge */}
            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display content__display--right">
                        <h2>The challenge</h2>
                    </div>

                    <div className="content__copy">
                        <p className="p-lead">
                            Interfacing with a newly-developed device always presents an interesting challenge,
                            particularly when the device can be controlled using software to{' '}
                            <span className="highlight">move</span> and implement{' '}
                            <span className="highlight">complex scenarios</span>.
                        </p>
                        <p>
                            As the TACTIC is used in complex, critical traffic situations, ensuring the{' '}
                            <span className="highlight">reliability</span> of the application was very important. The
                            software needed to undergo thorough testing in the field.
                        </p>
                        <p>
                            Controlling the hardware on the TACTIC was also a challenge. For example, it needed to be
                            possible, using the app, to fold the device open, turn it 45° to each side and close it
                            again. This needed to take place by controlling actuators and reading signals from sensors.
                            The 360° optical signage (e.g. rotating beacons) and the matrix sign also needed to be able
                            to be operated from the application.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            {/* Article idea and challenges description */}

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image image--detail">
                                <div className="image__content">
                                    <Img
                                        alt="The TACTIC interface to choose signs"
                                        fluid={data.tacticControl.childImageSharp.fluid}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="content__copy">
                            <h2>Operating the TACTIC</h2>
                            <p>
                                Symbols can be selected in the application to be displayed on the TACTIC’s matrix sign.
                                These symbols are used to provide information to road users, for example about{' '}
                                <span className="highlight">roadworks or an accident</span>.
                            </p>
                            <p>
                                The matrix sign can also display a text entered by the operator. The driver of the
                                assistance vehicle sets the text in the input field in the application. The application
                                is also used to control the optical signage, such as rotating beacons and flashing
                                lights.
                            </p>
                            <p>
                                Using the convenient export feature, a fleet manager can transfer all buttons that have
                                been preset and configured from one mobile device to another with ease.
                            </p>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__image content__image--right">
                        <div className="image image--detail">
                            <div className="image__content">
                                <Img
                                    alt="The TACTIC build on a service car"
                                    fluid={data.tacticPickup.childImageSharp.fluid}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="content__copy">
                        <h2>Opening, turning and closing</h2>
                        <p>
                            Smart communication with the hardware means the TACTIC can be folded open, turned 45&deg; to
                            each side and folded in again from the application, just by pushing a few buttons.
                        </p>
                        <p>
                            <span className="highlight">Actuators</span> on the device push the matrix sign up and bring
                            it into position, with <span className="highlight">sensors</span> providing the linked app
                            with continuous feedback on the exact position of the sign. The cover on the TACTIC doesn’t
                            just protect the device in bad weather conditions, it also means you can drive at up to 130
                            kph with an active matrix sign.
                        </p>
                        <p>
                            For a flexible and convenient UX we have added a feature allowing{' '}
                            <span className="highlight">scenarios</span> to be preset. This means you can push a single
                            button to (1) fold open the TACTIC, (2) start the optical signage flashing and (3) display
                            preset symbols on the matrix sign.
                        </p>
                        <p>
                            The entire scenario consists of various presettable steps, so that immediate action can be
                            taken by pushing a <span className="highlight">single</span> button when an urgent situation
                            arises on the road.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image image--detail">
                                <div className="image__content">
                                    <Img
                                        alt="Concept of the TACTIC iOS-app"
                                        fluid={data.tacticIPhones.childImageSharp.fluid}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="content__copy">
                            <h2>Communication protocol for iOS</h2>
                            <p>
                                Disperanto, the protocol for communication with the TACTIC’s matrix sign only had a
                                public library for Java. The same applied to the chips that we used to control the
                                relays for the actuators. As we were going to develop an iOS version of the application,
                                it was necessary to write Swift implementations of these two protocols as well.
                            </p>
                            <p>
                                For use in the iOS app we created{' '}
                                <a
                                    target="_blank"
                                    title="GitHub repository for dSRelay from Label305"
                                    href="https://github.com/Label305/dSRelay">
                                    our own libraries
                                </a>{' '}
                                in Swift for the use of both protocols. The library for controlling the relays is even
                                open source, enabling others in the development community to use it.
                            </p>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            {/* Make sure to have the correct spacing when br becomes invisible */}
                            Innovating <br /> together
                        </h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            The product team at TTI Partners works closely with our specialists to ensure we achieve the
                            best possible development.
                        </p>
                        <p>
                            The product owner at TTI Partners conducts <span className="highlight">detailed field</span>{' '}
                            tests for each development iteration to increase reliability. The well-thought-out agile
                            development process means we keep iterating to produce better software, so by working
                            together we make providing information to road users much safer for both traffic assistance
                            workers and road users.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="image">
                    <div className="image__content">
                        <Img alt="the TACTIC app in action" fluid={data.tacticAction.childImageSharp.fluid} />
                    </div>
                    <div className="image__caption">
                        <span className="ion-md-arrow-dropright">
                            The TACTIC in action. Safe, reliable remote control is possible with the TACTIC.
                        </span>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            {/* Make sure to have the correct spacing when br becomes invisible */}
                            Looking to <br /> develop an <br /> innovative <br /> product too?
                        </h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            The TACTIC is used internationally to improve safety on our roads. If you are also
                            interested in enriching an innovative product with native applications then get in touch
                            with{' '}
                            <span className="inline-avatar">
                                <Img alt="Joris" fixed={data.joris.childImageSharp.fixed} Tag="span" />
                                <span>Joris</span>
                            </span>{' '}
                            to discuss your plans.
                        </p>
                    </div>
                    <div className="content__buttons content__buttons--50">
                        <Button
                            url="/en/contact/"
                            name="Contact us"
                            title="Contact information Enschede and Amsterdam"
                        />
                    </div>
                </div>
            </WillAnimate>
        </section>
    </Layout>
);

export const query = graphql`
    query {
        tactic: file(relativePath: {eq: "portfolio/tactic/tactic-main.jpg"}) {
            ...fullscreenHero
        }
        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }
        tacticDevice: file(relativePath: {eq: "portfolio/tactic/tactic-device.jpg"}) {
            ...fluid1200Width
        }
        tacticControl: file(relativePath: {eq: "portfolio/tactic/tactic-control.png"}) {
            ...fluid800Width
        }
        tacticPickup: file(relativePath: {eq: "portfolio/tactic/tactic-pickup.jpg"}) {
            ...fluid800Width
        }
        tacticIPhones: file(relativePath: {eq: "portfolio/tactic/tactic-iphones.png"}) {
            ...fluid800Width
        }
        tacticAction: file(relativePath: {eq: "portfolio/tactic/tactic-action.jpg"}) {
            ...fluid1200Width
        }
    }
`;
